import React, { Component } from 'react';
import async from './async'


function processContetnOrderChange(el, elPrev, chapters) {
  // 1. el is a topic
      //1. move from chapter to chapter
    if (el.type === 'topic') {
      var prevParent = {}
      var newParent = {}
      // js:3 lIndex changes inside old parent
      chapters.forEach( function(el_1) {
        if (el_1.id === el.parentId) {
          prevParent = el_1
        }
      });
      // calculate new state of el
      if (chapters[el.gIndex-1].type === 'chapter' ) {
        el.lIndex = 0
        el.parentId = chapters[el.gIndex-1].id
      } else {
        el.lIndex = chapters[el.gIndex-1].lIndex
        el.parentId = chapters[el.gIndex-1].parentId
      }
      // js:2 lIndex changes inside new parent
      chapters.forEach(function(el_1) {
        if (el_1.id === el.parentId) {
          newParent = el_1
        }
      });

      if (newParent !== prevParent) {
        newParent.children++
        prevParent.children--
      }

      var lIndex = 0;
      for (var i2 = 0, e2 = chapters.length - 1; i2 <= e2; i2++) {
        if (chapters[i2].type === 'chapter') {
          // skip and start lIndex
          lIndex = 0;
        } else {
          chapters[i2].lIndex = lIndex++;
        }
      }
      
      async.tasks.push({
        task: 'TRY_MOVE_FILE', // also update lIndex
        fromFolderId: elPrev.parentId,
        toFolderId: el.parentId,
        fileId: el.id,
        chapters: [].concat(chapters)
      })
      // console.log(el, elPrev)
    }
  // 3. el is a chapter
    // 1. only gIndex can be changed so update the note.json
    if (el.type === 'chapter') {
      async.tasks.push({
        task: 'CHANGE_CHAPTER_INDEX', // also update lIndex
        chapters: [].concat(chapters)
      })    
    }
    if (!async.running && async.tasks.length !== 0) {
      async.run()
    }
}


class ContentList extends Component {
  constructor (props) {
    super(props)
    var self = this
    this.timer = 0
    this.mousedown = false
    this.dropEl = ''
    this.el = ''
    this.elId =''
    this.elChildren = []
    this.editing = false
    this.editEl = -1
    this.editOldName = ''
    this.creatingTopic = false
    // async.createMetadata(this.props.parent.state.chapters)
    // create the Note object here pass inside async
    window.onmouseup = function (e) {
      self.onMouseUp(e)
    }
  }

  contextMenu (e) {
    e.preventDefault();
    console.log(e)
    // addMenu.popup(e.clientX, e.clientY);
  }

  onMouseMoveInput (e) {
    e.stopPropagation()
  }

  onMouseUp (event) {
    // event.preventDefault()
    var chapters = this.props.parent.state.chapters
    var elPrev = Object.assign({}, this.el)
    var elIndex = 0;
    var self = this
    // add chapter children to list
    if (this.dropEl && this.el !== '' && this.dropEl.id !== this.el.id) {
      chapters.splice(this.dropIndex, 0, this.el);
      this.elChildren.forEach((el, index)=>{
        chapters.splice(self.dropIndex+index+1, 0, el)
      });
      this.elChildren = []

      // find update the list
      chapters.forEach((el, index)=>{
        if (this.el.id === el.id) {
          elIndex = index
        }
      });
      this.consistentgIndex()

      // process the chapters list
      // sanitize and save
      processContetnOrderChange(chapters[elIndex], elPrev, chapters);

      this.props.parent.setState({
        chapters: chapters
      })   
    }
    this.el = ''
    this.elId = ''
    this.mousedown = false;
  }

  consistentgIndex () {
    this.props.parent.state.chapters.forEach((el, index)=>{
      el.gIndex = index;
      el.class = '';
    });
  }

  onLinkClick (el) {
    this.props.onSelect(el.id)
  }

  onChange (e, el) {
    for(var j = 0, length2 = this.props.children.length; j < length2; j++){
      this.props.children[j].selected = false
    }
    var index = this.props.children.indexOf(el)
    this.props.children[index].name = e.target.value
    this.props.parent.setState({
      chapters: this.props.children
    })
  }

  onKeyPress (e, el) {
    var index = this.props.children.indexOf(el)
    if (e.key === 'Enter') {
    console.log(el)
      // enter pressed
      // make the input to a link
      this.props.children[index].name = this.props.children[index].name.trim(' ')
      this.props.children[index].edit = false
      this.props.children[index].selected = true
      this.props.parent.setState({
        chapters: this.props.children
      })
      this.editing = false
      this.editEl = -1
      this.registerRenameAsyncTask(el)
    }
  }

  registerRenameAsyncTask (el) {
    // sync cloud 
    if (this.creatingTopic) {
      async.tasks.push({
        task: 'CREATE_TOPIC', // also update lIndex
        el: el,
        chapters: [].concat(this.props.parent.state.chapters)
      })     
      this.creatingTopic = false
    } else {
      var editOldName  = this.editOldName
      if (el.type === 'chapter') {
        async.tasks.push({
          task: 'RENAME_CHAPTER', // also update lIndex
          newName: el.name,
          oldName: editOldName,
          fileId: el.id,
          chapters: [].concat(this.props.parent.state.chapters)
        })     
      } else if (el.type === 'topic') {
        async.tasks.push({
          task: 'RENAME_TOPIC', // also update lIndex
          newName: el.name,
          oldName: editOldName,
          fileId: el.id,
          chapters: [].concat(this.props.parent.state.chapters)
        })     
      }
    }

    if (!async.running && async.tasks.length !== 0) {
      async.run()
    }
  }

  onMouseDown (el, event) {
    if (!this.editing) {
      event.preventDefault()
    }
    this.mousedown = true;
    this.elId = el.id
  }

  findDownPosition (index) {
    var chapters = this.props.parent.state.chapters;
    for (var start = index, end = chapters.length - 1; start <= end; start++) {
      if (!chapters[start+1] || !chapters[start+1].parentId) {
        chapters[start].class = 'bottom-4'
        // this.dropIndex = start+1
        return start+1
      }
    }
  }

  onMouseMove (el, event) {
    var chapters = this.props.parent.state.chapters;
    if (!this.editing) {
      event.preventDefault()
    } else {
      return
    }
    if (this.mousedown) {
      if (this.elId !== '' && this.elId === el.id) {
        this.el = el
      }
      // if (this.target.className.split(' ').indexOf('up-2') === -1) {
      //   this.target.className = this.target.className + ' up-2';
      // } else {
      // remove the el

      if (this.el.id === el.id) {
        var index = chapters.indexOf(el)
        if (el.type === 'chapter') {
          if (chapters.length === 1) {
            return
          }
        }
        if (index !== -1) {
          // remove chapter and its topics
          chapters.splice(index, 1)
          if (el.type === 'chapter' && el.children !== 0) {
            // ss rotates at same position
            for (var ss = index;;) {
              if (chapters[ss] && chapters[ss].type === 'topic') {
                this.elChildren.push(Object.assign({}, chapters[ss]))
                chapters.splice(ss, 1)
                if (!chapters[ss] || chapters[ss].type === 'chapter') {
                  break;
                }
              } else {
                break
              }
            }
          }

          chapters.forEach((el, index)=>{
            el.gIndex = index;
          })
          // console.log(chapters.length)
          this.props.parent.setState({
            chapters: chapters
          })
        }
      }
      
      // 
      if(el.id !== this.el.id) {
        var rectObject = event.target.getBoundingClientRect();
        var onBottomHalf = false
        if (event.clientY > (rectObject.top + (rectObject.bottom-rectObject.top)/2)) {
          onBottomHalf = true
        }
        this.dropEl = el
        chapters.forEach((_el, _index)=>{
          _el.class = '';
          _el.gIndex = _index;
          if (_el.id === el.id) { index = _index }
        })

        if (this.el.type === 'chapter') {
          if (onBottomHalf) {
            if (el.type === 'chapter') {
              if (chapters[index+1] && chapters[index+1].type === 'chapter') {
                chapters[index].class = 'bottom-4'
                this.dropIndex = index+1
              } else {
                // go to end
                this.dropIndex = this.findDownPosition(index)
              }
            }
            if (el.type === 'topic') {
              // go to end
              this.dropIndex = this.findDownPosition(index)
            }
          } else {
            // onTopHalf
            if (el.type === 'chapter') {
              if (index === 0) {
                chapters[index].class = 'top-4'
              } else {
                chapters[index-1].class = 'bottom-4'
              }
              this.dropIndex = index
            } else {
              // go to end
              this.dropIndex = this.findDownPosition(index)
            }
          }
        } else {
          // this.el.type === 'topic'
          if (onBottomHalf) {
              chapters[index].class = 'bottom-4'
              this.dropIndex = index+1
            } else {
              // if el is last element
              if (index === 0) {
                chapters[index].class = 'bottom-4'
                this.dropIndex = 1
              } else {
                chapters[index-1].class = 'bottom-4'
                this.dropIndex = index
              }
          }
        }

        this.props.parent.setState({
          chapters: chapters
        })
      }
    }
  }

  onBlur (e) {
    var self = this 
    var el = this.props.children[this.editEl]
    this.props.children[this.editEl].name = this.props.children[this.editEl].name.trim(' ')
    this.props.children[this.editEl].edit = false
    this.props.children[this.editEl].selected = true
    this.props.parent.setState({
      chapters: self.props.children
    })

    this.editing = false
    this.editEl = -1
    // sync cloud
    this.registerRenameAsyncTask(el)
  }

  editLink (e, el) {
    this.editing = true
    var chapters = this.props.parent.state.chapters

    this.editEl = chapters.indexOf(el)
    chapters[this.editEl].edit = true
    this.props.parent.setState({
      chapters: chapters
    })

    // sync cloud
  }

  addTopic (e, el) {
    console.log(el.gIndex)
    var addTopicIndex = this.findDownPosition(el.gIndex)
    var chapters = this.props.parent.state.chapters
    chapters.forEach((_el)=>{
      _el.selected = false
    })
    var newTopic = Object.assign({
      id: this.props.parent.state.chapters.length,
      name: 'New Topic',
      edit: true,
      selected: true,
      type: 'topic',
      parentId: el.id
    })
    el.children++
    chapters.splice(addTopicIndex, 0, newTopic)
    
    this.consistentgIndex()
    this.props.parent.setState({
      chapters: chapters
    })

    this.editEl = addTopicIndex
    this.editing = true

    // create a topic file on drive
    this.creatingTopic = true
  }

  componentDidUpdate() {
    if (this.editing) {
      this.refs.editInput.focus()
      this.editOldName = this.refs.editInput.value
    }
  }

  /**
   * acivate link on select
   * creates the content list sidebar content
   */
  render () {
    // var addEl = (this.props.chapters) ? <span className="c-ctrl add-topic">+</span> : ''

    // console.log(this.props.children)
    var listEl = this.props.children.map((el, index)=>{
      var addEl = ''
      if (el.type === 'chapter') {
        addEl = <span className="c-ctrl add-topic" onMouseUp={(e)=>{this.addTopic(e, el)}}>
                  <span className="glyphicon glyphicon-plus form-control-feedback" aria-hidden="true"></span>
                </span>
      }
      if ((el.type === 'chapter' && el.children === 0) || el.type === 'topic') {
        var liTopic = ''
        if (el.type === 'topic') {
          liTopic = 'li-topic'
        }
        return <li className={"simple-li " + liTopic + " el-" + el.id} key={index}
          onMouseDown={(e)=>{
            this.onLinkClick(el)
            this.onMouseDown(el, e)
          }}
          onMouseMove={(e)=>{this.onMouseMove(el, e)}}>
          
          {(el.edit)?<input ref="editInput" type="text" value={el.name} 
            onKeyPress={(e)=>{this.onKeyPress(e, el)}} 
            onChange={(e)=>{this.onChange(e, el)}}
            onMouseMove={this.onMouseMoveInput.bind(this)}
            onBlur={this.onBlur.bind(this)}
          />:''}

          {(!el.edit)?<span className={el.selected?'note-link-topic active':'note-link-topic'} 
            >{el.name}
            {addEl}
            <span className="c-ctrl edit-name" onMouseUp={(e)=>{this.editLink(e, el)}}>
              <span className="glyphicon glyphicon-pencil form-control-feedback" aria-hidden="true"></span>
            </span>
            </span>:''}

          {el.gIndex === 0 && el.class === 'top-4' ? <div className="top-4"></div>:''}
          {el.class === 'bottom-4' ? <div className="bottom-4"></div>:''}
        </li>
      } else {
        return <li className={"simple-li el-" + el.id} key={index}
          onContextMenu={this.contextMenu} 

          onMouseDown={(e)=>{
            this.onLinkClick(el)
            this.onMouseDown(el, e)
          }}
          onMouseMove={(e)=>{this.onMouseMove(el, e)}}>

          {(el.edit)?<input ref="editInput" type="text" value={el.name} 
            onKeyPress={(e)=>{this.onKeyPress(e, el)}} 
            onChange={(e)=>{this.onChange(e, el)}}
            onMouseMove={this.onMouseMoveInput.bind(this)}
            onBlur={this.onBlur.bind(this)}
            />:''}

          {(!el.edit) ? <span className={el.selected?'note-link-chapter bold active':'note-link-chapter bold'}
            >{el.name}
            {addEl}
            <span className="c-ctrl edit-name" onMouseUp={(e)=>{this.editLink(e, el)}}>
              <span className="glyphicon glyphicon-pencil form-control-feedback" aria-hidden="true"></span>
            </span>
          </span>:''}

          {el.gIndex === 0 && el.class === 'top-4' ? <div className="top-4"></div>:''}
          {el.class === 'bottom-4' ? <div className="bottom-4"></div>:''}
        </li>
      }
    })

    return (
      <ul className="simple-ul no-bold">
        {listEl}
      </ul>
    )
  }
}

export default ContentList;


