function createDirWithExt(parentId, dirName, extension, cb) {
  createDirIfNotExists(dirName+extension, parentId, cb)
}

function createFileIfNotExists(name, parentId, cb) {
  var qp = '';
  if (parentId !== '/') {// if not a parent provided create at rootDir
    qp = "'" + parentId + "' in parents and ";
  }

  // get all files in parentId
  gapi.client.drive.files.list({
    'q': qp + "name='" + name  + "' and trashed != true",
    'pageSize': 5,
    'fields': "nextPageToken, files(id, name)"
  }).execute(function(response) {
    if (response.message == 'Invalid Value') {
      console.log('Error...', response);
      return
    }

    if (!response.files) {
      console.error('No files found');
    }

    /*Create a initial root folder*/
    if (response.files.length == 0) {
      var fileMetadata = {
        'name' : name,
        'mimeType' : 'text/plain'
      };

      if (parentId != '/') {
        fileMetadata.parents = [ parentId ]
      }

      gapi.client.drive.files.create({
         resource: fileMetadata,
         fields: 'id, name'
      }).execute(function(resp, raw_resp) {
        cb(resp);
      });
      return
    }

    if (response.files.length == 1) {
      // ok
      cb(response.files[0]);
    } else {
      console.log('Already exists');
    }
  });
  // create if not exists
  // pass to cb
}

